croll {\n  flex: 1;\n  min-height: 0;\n  overflow: auto;\n  padding: 8px;\n  display: flex;\n  flex-direction: column;\n  gap: 7px;\n}\n\n.wb-history-version-item {\n  border: 1px solid var(--wb-border-main);\n  border-radius: 8px;\n  padding: 6px;\n  display: grid;\n  gap: 4px;\n  background: var(--wb-bg-panel);\n}\n\n.wb-history-version-line {\n  display: flex;\n  justify-content: space-between;\n  gap: 6px;\n  align-items: center;\n}\n\n.wb-history-version-line strong {\n  font-size: 11px;\n  color: var(--wb-text-main);\n}\n\n.wb-history-version-item span {\n  font-size: 11px;\n  color: var(--wb-text-muted);\n  word-break: break-all;\n}\n\n.wb-history-lr {\n  display: inline-flex;\n  gap: 4px;\n}\n\n.mini-lr {\n  border: 1px solid var(--wb-border-main);\n  background: var(--wb-input-bg);\n  color: var(--wb-text-muted);\n  border-radius: 6px;\n  min-width: 24px;\n  font-size: 10px;\n  cursor: pointer;\n}\n\n.mini-lr.active {\n  border-color: #22d3ee;\n  color: #67e8f9;\n}\n\n.wb-history-diff-wrap {\n  min-height: 0;\n  display: flex;\n  flex-direction: column;\n}\n\n.wb-history-diff-head {\n  border-bottom: 1px solid var(--wb-border-main);\n  background: var(--wb-bg-panel);\n  padding: 8px 10px;\n  display: flex;\n  justify-content: space-between;\n  gap: 8px;\n  align-items: center;\n  font-size: 11px;\n  color: var(--wb-text-muted);\n}\n\n.wb-history-diff-grid {\n  min-height: 0;\n  flex: 1;\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n}\n\n.wb-history-diff-grid > div {\n  min-height: 0;\n  display: flex;\n  flex-direction: column;\n}\n\n.wb-history-diff-grid > div + div {\n  border-left: 1px solid var(--wb-border-main);\n}\n\n.wb-history-diff-title {\n  padding: 8px 10px;\n  border-bottom: 1px solid var(--wb-border-main);\n  font-size: 11px;\n  color: var(--wb-primary-light);\n}\n\n.wb-history-diff-body {\n  min-height: 0;\n  flex: 1;\n  overflow: auto;\n  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;\n  font-size: 11px;\n  background: var(--wb-input-bg-focus);\n}\n\n.diff-row {\n  display: grid;\n  grid-template-columns: 54px 1fr;\n  align-items: start;\n  border-bottom: 1px solid var(--wb-border-subtle);\n}\n\n.line-no {\n  color: var(--wb-text-muted);\n  padding: 2px 8px;\n  border-right: 1px solid var(--wb-border-subtle);\n  user-select: none;\n}\n\n.line-text {\n  white-space: pre-wrap;\n  word-break: break-word;\n  padding: 2px 8px;\n  color: var(--wb-text-main);\n}\n\n.diff-row.add {\n  background: rgba(34, 197, 94, 0.18);\n}\n\n.diff-row.del {\n  background: rgba(239, 68, 68, 0.18);\n}\n\n.diff-row.empty {\n  background: rgba(100, 116, 139, 0.08);\n}\n\n@media (max-width: 1380px) {\n  .wb-editor-shell {\n    grid-template-columns: 1fr;\n  }\n\n  .editor-side {\n    overflow: visible;\n    max-height: none;\n  }\n\n  .wb-history-modal-main {\n    grid-template-columns: 1fr;\n  }\n\n  .wb-history-versions {\n    border-right: none;\n    border-bottom: 1px solid var(--wb-border-main);\n    max-height: 240px;\n  }\n}\n\n@media (max-width: 1100px) {\n  .global-mode-grid {\n    grid-template-columns: 1fr;\n  }\n\n  .wb-resize-handle {\n    display: none;\n  }\n\n  .wb-main-layout {\n    grid-template-columns: 1fr;\n  }\n\n  .wb-tools-grid {\n    grid-template-columns: 1fr;\n  }\n\n  .editor-grid.two-cols,\n  .editor-grid.three-cols {\n    grid-template-columns: 1fr;\n  }\n\n  .editor-head {\n    flex-direction: column;\n    align-items: stretch;\n  }\n\n  .editor-badges {\n    justify-content: flex-start;\n  }\n\n  .history-preview-grid {\n    grid-template-columns: 1fr;\n  }\n\n  .wb-history-diff-grid {\n    grid-template-columns: 1fr;\n  }\n\n  .wb-history-diff-grid > div + div {\n    border-left: none;\n    border-top: 1px solid var(--wb-border-main);\n  }\n\n  .wb-status {\n    flex-direction: column;\n  }\n\n\n  .wb-floating-window {\n    width: calc(100vw - 16px) !important;\n    left: 8px !important;\n    right: 8px;\n  }\n}\n\n.editor-back-btn {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  padding: 10px 12px;\n  margin: 0 0 8px 0;\n  background: var(--wb-bg-panel);\n  border: none;\n  border-radius: 6px;\n  color: var(--wb-primary);\n  font-weight: 600;\n  cursor: pointer;\n}\n\n.editor-back-btn:hover {\n  background: var(--wb-primary-hover);\n}\n\n/*  Mobile Tab View  */\n.mobile-tab-view {\n  display: flex;\n  flex-direction: column;\n  flex: 1;\n  min-height: 0;\n  overflow: hidden;\n}\n\n.mobile-tab-content {\n  flex: 1;\n  min-height: 0;\n  position: relative;\n}\n\n.mobile-pane {\n  position: absolute;\n  inset: 0;\n  overflow-y: auto;\n  padding: 8px;\n  -webkit-overflow-scrolling: touch;\n}\n\n.mobile-entry-list {\n  display: flex;\n  flex-direction: column;\n  gap: 4px;\n}\n\n.mobile-ai-panel {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n}\n\n.mobile-ai-panel .ai-chat-area {\n  flex: 1;\n  min-height: 0;\n  display: flex;\n  flex-direction: column;\n}\n\n.mobile-danger-zone {\n  display: flex;\n  gap: 8px;\n  margin-top: 16px;\n  padding-top: 16px;\n  border-top: 1px solid var(--wb-border);\n}\n\n.mobile-danger-zone .btn {\n  flex: 1;\n}\n\n.mobile-tab-bar {\n  position: fixed;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  z-index: 10100;\n  display: flex;\n  border-top: 1px solid var(--wb-border);\n  background: var(--wb-bg-secondary);\n  height: 52px;\n}\n\n.mobile-tab-bar button {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 2px;\n  border: none;\n  background: transparent;\n  color: var(--wb-text-dim);\n  font-size: 10px;\n  padding: 4px 0;\n  cursor: pointer;\n  transition: color 0.15s, background 0.15s;\n  -webkit-tap-highlight-color: transparent;\n}\n\n.mobile-tab-bar button.active {\n  color: var(--wb-accent);\n  background: color-mix(in srgb, var(--wb-accent) 8%, transparent);\n}\n\n.mobile-tab-bar .tab-icon {\n  font-size: 20px;\n  line-height: 1;\n}\n\n.mobile-tab-bar .tab-label {\n  font-weight: 500;\n}\n\n@media (max-width: 768px) {\n  .wb-assistant-root {\n    padding: 6px;\n    gap: 8px;\n  }\n\n  .wb-header,\n  .wb-bindings,\n  .global-mode-panel,\n  .wb-toolbar {\n    padding: 6px;\n    gap: 6px;\n  }\n\n  .toolbar-label {\n    min-width: 100%;\n  }\n\n  .toolbar-select {\n    width: 100%;\n  }\n\n  .worldbook-picker-trigger {\n    white-space: normal;\n  }\n\n  .wb-main-layout {\n    display: block !important;\n    height: auto !important;\n    overflow: visible !important;\n  }\n\n  .wb-entry-list,\n  .wb-editor,\n  .wb-editor-shell,\n  .editor-side {\n    width: 100% !important;\n    height: auto !important;\n    max-height: none !important;\n    border: none !important;\n    padding: 0 !important;\n  }\n\n  .wb-entry-list {\n    max-height: 80vh !important;\n    overflow-y: auto;\n  }\n\n  .wb-editor-shell {\n    display: block !important;\n  }\n\n  .list-scroll {\n    max-height: 60vh;\n  }\n\n  .wb-floating-window {\n    left: 8px !important;\n    right: 8px !important;\n    width: auto !important;\n    max-width: none !important;\n  }\n}\n\n.list-actions {\n  padding: 0 8px;\n}\n\n/* \n   Override: Force theme colors on native form elements\n   This beats SillyTavern global dark CSS which sets\n   background/color on textarea, input, select, etc.\n    */\n.wb-assistant-root input,\n.wb-assistant-root textarea,\n.wb-assistant-root select,\n.wb-assistant-root option,\n.wb-assistant-root button {\n  color: var(--wb-text-main) !important;\n}\n\n.wb-assistant-root input[type=\"text\"],\n.wb-assistant-root input[type=\"number\"],\n.wb-assistant-root textarea,\n.wb-assistant-root select {\n  background: var(--wb-input-bg) !important;\n  border-color: transparent !important;\n}\n\n.wb-assistant-root input[type=\"text\"]:hover,\n.wb-assistant-root input[type=\"number\"]:hover,\n.wb-assistant-root textarea:hover,\n.wb-assistant-root select:hover {\n  background: var(--wb-input-bg-hover) !important;\n}\n\n.wb-assistant-root input[type=\"text\"]:focus,\n.wb-assistant-root input[type=\"number\"]:focus,\n.wb-assistant-root textarea:focus,\n.wb-assistant-root select:focus {\n  background: var(--wb-input-bg-focus) !important;\n  border-color: var(--wb-primary-glow) !important;\n  outline: none !important;\n  box-shadow: 0 0 0 3px var(--wb-primary-soft) !important;\n}\n/* \n   AI Generator Panel\n    */\n.ai-generator-panel {\n  display: flex;\n  gap: 0;\n  flex: 1;\n  min-height: 0;\n  border-radius: var(--wb-radius);\n  overflow: hidden;\n  background: var(--wb-bg-secondary);\n}\n\n/*  Sidebar  */\n.ai-sidebar {\n  width: 220px;\n  min-width: 180px;\n  border-right: 1px solid var(--wb-border);\n  display: flex;\n  flex-direction: column;\n  background: var(--wb-bg-main);\n}\n\n.ai-sidebar-head {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 10px 12px;\n  border-bottom: 1px solid var(--wb-border);\n}\n\n.ai-sidebar-title {\n  font-weight: 600;\n  font-size: 0.9em;\n  color: var(--wb-text-main);\n}\n\n.ai-session-list {\n  flex: 1;\n  overflow-y: auto;\n  padding: 6px;\n}\n\n.ai-session-item {\n  display: flex;\n  flex-wrap: wrap;\n  align-items: center;\n  width: 100%;\n  padding: 8px 10px;\n  margin-bottom: 2px;\n  border: none;\n  border-radius: var(--wb-radius-sm, 6px);\n  background: transparent;\n  cursor: pointer;\n  text-align: left;\n  transition: background 0.15s;\n  position: relative;\n}\n\n.ai-session-item:hover {\n  background: var(--wb-bg-highlight);\n}\n\n.ai-session-item.active {\n  background: var(--wb-primary-soft);\n}\n\n.ai-session-title {\n  flex: 1;\n  font-size: 0.85em;\n  color: var(--wb-text-main);\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n.ai-session-meta {\n  font-size: 0.72em;\n  color: var(--wb-text-dim);\n  width: 100%;\n  margin-top: 2px;\n}\n\n.ai-session-delete {\n  position: absolute;\n  top: 4px;\n  right: 4px;\n  width: 20px;\n  height: 20px;\n  border: none;\n  background: transparent;\n  color: var(--wb-text-dim);\n  font-size: 1em;\n  cursor: pointer;\n  border-radius: 50%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  opacity: 0;\n  transition: opacity 0.15s, background 0.15s;\n}\n\n.ai-session-item:hover .ai-session-delete {\n  opacity: 1;\n}\n\n.ai-session-delete:hover {\n  background: var(--wb-danger, #e74c3c);\n  color: #fff;\n}\n\n/*  Chat Area  */\n.ai-chat-area {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  min-width: 0;\n}\n\n.ai-chat-empty {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  gap: 12px;\n  color: var(--wb-text-dim);\n}\n\n.ai-chat-empty-icon {\n  font-size: 3em;\n  opacity: 0.5;\n}\n\n.ai-chat-empty-text {\n  font-size: 0.95em;\n}\n\n.ai-chat-messages {\n  flex: 1;\n  overflow-y: auto;\n  padding: 16px;\n  display: flex;\n  flex-direction: column;\n  gap: 12px;\n}\n\n/*  Chat bubbles  */\n.ai-chat-bubble {\n  max-width: 85%;\n  padding: 10px 14px;\n  border-radius: 12px;\n  line-height: 1.55;\n  white-space: pre-wrap;\n  word-break: break-word;\n}\n\n.ai-chat-bubble.user {\n  align-self: flex-end;\n  background: var(--wb-primary-soft);\n  border-bottom-right-radius: 4px;\n}\n\n.ai-chat-bubble.assistant {\n  align-self: flex-start;\n  background: var(--wb-bg-main);\n  border: 1px solid var(--wb-border);\n  border-bottom-left-radius: 4px;\n}\n\n.ai-chat-bubble-role {\n  font-size: 0.72em;\n  font-weight: 600;\n  color: var(--wb-text-dim);\n  margin-bottom: 4px;\n}\n\n.ai-chat-bubble-content {\n  font-size: 0.88em;\n  color: var(--wb-text-main);\n}\n\n.ai-cursor {\n  animation: ai-blink 0.7s infinite;\n  color: var(--wb-primary);\n}\n\n@keyframes ai-blink {\n  0%, 100% { opacity: 1; }\n  50% { opacity: 0; }\n}\n\n.ai-thinking {\n  color: var(--wb-text-dim);\n  font-style: italic;\n}\n\n/*  Input bar  */\n.ai-chat-input-bar {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 8px;\n  padding: 12px 16px;\n  border-top: 1px solid var(--wb-border);\n  background: var(--wb-bg-main);\n  align-items: flex-end;\n}\n\n.ai-context-toggle {\n  width: 100%;\n  display: flex;\n  align-items: center;\n  gap: 6px;\n  font-size: 0.78em;\n  color: var(--wb-text-dim);\n  cursor: pointer;\n  user-select: none;\n}\n\n.ai-context-toggle input {\n  margin: 0;\n}\n\n.ai-context-toggle span {\n  white-space: nowrap;\n}\n\n.ai-chat-input {\n  flex: 1;\n  resize: vertical;\n  min-height: 40px;\n  max-height: 140px;\n  font-size: 0.88em;\n}\n\n.ai-send-btn,\n.ai-stop-btn {\n  min-width: 64px;\n  height: 40px;\n}\n\n/* \n   Tag Review Modal\n    */\n.ai-tag-review-overlay {\n  position: fixed;\n  inset: 0;\n  z-index: 9999;\n  background: rgba(0, 0, 0, 0.55);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.ai-tag-review-modal {\n  background: var(--wb-bg-main);\n  border-radius: var(--wb-radius);\n  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.35);\n  width: 580px;\n  max-width: 92vw;\n  max-height: 80vh;\n  display: flex;\n  flex-direction: column;\n  overflow: hidden;\n}\n\n.ai-tag-review-head {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: 14px 18px;\n  border-bottom: 1px solid var(--wb-border);\n}\n\n.ai-tag-review-title {\n  font-weight: 600;\n  font-size: 1em;\n  color: var(--wb-text-main);\n}\n\n.ai-tag-review-close {\n  width: 28px;\n  height: 28px;\n  border: none;\n  background: transparent;\n  color: var(--wb-text-dim);\n  font-size: 1.2em;\n  cursor: pointer;\n  border-radius: 50%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.ai-tag-review-close:hover {\n  background: var(--wb-bg-highlight);\n}\n\n.ai-tag-review-target {\n  padding: 12px 18px;\n  border-bottom: 1px solid var(--wb-border);\n}\n\n.ai-tag-list {\n  flex: 1;\n  overflow-y: auto;\n  padding: 8px 18px;\n}\n\n.ai-tag-item {\n  display: flex;\n  align-items: flex-start;\n  gap: 10px;\n  padding: 10px 0;\n  border-bottom: 1px solid var(--wb-border);\n 